:where(*) {
  box-sizing: border-box;
  text-align: start;
  border: 0 solid;
  margin: 0;
  padding: 0;
}

:where(html) {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-size: 100%;
}

main {
  width: 100%;
  display: block;
}

:where(h1, h2, h3, h4, h5, h6, p, small, span, strong, em, a, ul, ol, li, div, img, input, button, textarea, select, form, label, table, thead, tbody, tr, th, td, hr, nav, header, footer, section, article, main, aside, figure, figcaption, blockquote, pre, code, video, audio, canvas, iframe, details, summary, fieldset, legend, progress, meter, time, mark, output, datalist, menu, menuitem, menuitem, summary, details, summary, template) {
  font-size: var(--font-sm);
  font-family: var(--font-sans);
  margin: 0;
  padding: 0;
}

:where(h1, h2, h3, h4, h5, h6, p, small, span, strong, em, a, ul, ol, li, div, img, input, button, textarea, select, form, label, table, thead, tbody, tr, th, td, hr, nav, header, footer, section, article, main, aside, figure, figcaption, blockquote, video, audio, canvas, iframe, details, summary, fieldset, legend, progress, meter, time, mark, output, datalist, menu, menuitem, menuitem, summary, details, summary, template) {
  margin: 0;
  padding: 0;
  font-size: 1em;
}

:where(pre, code) {
  font-family: var(--font-mono) !important;
}

hr {
  box-sizing: border-box;
  border-top: var(--px, 1.5px) solid;
  height: 0;
  overflow: visible;
}

pre {
  font-size: 1em;
}

a {
  background-color: #0000;
}

